// ================================================
// GRID LINES
// ================================================

// cleafix the grid lines
.grid:before,
.grid:after {
    content: "";
    display: table;
}

.grid:after {
    clear: both;
}

.grid {
    zoom: 1;
    font-size: 0;
}

// ================================================
// GRID UNITS
// ================================================

.grid .unit {
    display: inline-block;
    vertical-align: top;
    font-size: 16px;
}

.grid > .line {
    display: block;
}

