// ================================================
// HORIZONTAL SPACING
// ================================================

.grid-spacing {
    margin-left: -3%;
}

.grid-spacing > .unit {
    padding-left: 3%;
}

.grid-spacing > *:not(.unit) {
    padding-left: 3%;
}

// ================================================
// horizontal spacing -- extra small
// ================================================

.grid-hxs {
    margin-left: -5px;
}

.grid-hxs > .unit {
    padding-left: 5px;
}

.grid-hxs > *:not(.unit) {
    margin-left: 5px;
}

// ================================================
// horizontal spacing -- small
// ================================================

.grid-hs {
    margin-left: -12px;
}

.grid-hs > .unit {
    padding-left: 12px;
}

.grid-hs > *:not(.unit) {
    margin-left: 12px;
}

// ================================================
// horizontal spacing -- medium
// ================================================

.grid-h,
.grid-hm {
    margin-left: -20px;
}

.grid-h > .unit,
.grid-hm > .unit {
    padding-left: 20px;
}

// ================================================
// horizontal spacing -- large
// ================================================

.grid-hl {
    margin-left: -30px;
}

.grid-hl > .unit {
    padding-left: 30px;
}

// ================================================
// horizontal spacing -- extra large
// ================================================

.grid-hxl {
    margin-left: -40px;
}

.grid-hxl > .unit {
    padding-left: 40px;
}

// ================================================
// horizontal spacing -- extra extra large
// ================================================

.grid-hxxl {
    margin-left: -50px;
}

.grid-hxxl > .unit {
    padding-left: 50px;
}

// ================================================
// VERTICAL SPACING
// ================================================

.grid-vxs > .unit {
    margin-bottom: 5px;
}

.grid-vs > .unit {
    margin-bottom: 12px;
}

.grid-v  > .unit,
.grid-vm > .unit {
    margin-bottom: 20px;
}

.grid-vl > .unit {
    margin-bottom: 30px;
}

.grid-vxl > .unit {
    margin-bottom: 40px;
}

.grid-vxxl > .unit {
    margin-bottom: 50px;
}

// ================================================
// LAST UNIT
// ================================================

.last-vn > .unit:last-child {
    margin-bottom: 0;
}

.last-vxs > .unit:last-child {
    margin-bottom: 5px;
}

.last-vs > .unit:last-child {
    margin-bottom: 12px;
}

.last-v > .unit:last-child,
.last-vm > .unit:last-child {
    margin-bottom: 20px;
}

.last-vl > .unit:last-child {
    margin-bottom: 30px;
}

.last-vxl > .unit:last-child {
    margin-bottom: 40px;
}

.last-vxxl > .unit:last-child {
    margin-bottom: 50px;
}

